
import Vue from 'vue'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import Selector from 'vux/src/components/selector/index.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Panel from 'vux/src/components/panel/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

Vue.use(LoadingPlugin)
export default {
  directives: {
    TransferDom
  },
  components: {
    Tabbar,
    Selector,
    TabbarItem,
    Panel,
    Group,
    XButton,    
    PopupPicker,
    Confirm,
    GridItem,
    Grid,
    Toast
  },
  data () {
      return {
          joblist: [],
          status: [],
          selectjob: '',
          show: false,
          showPositionValue: false,
          tips:"",
          idx:0,
          jobIdx:0,//
          levIdx:0,//
          items:[],//
          base:{},//
          phaseNames: [],
          phaseName: "",
          statusName: "",
          globalMes:'',
          describeTxt:'--'// or  or --
      }
  },
  created() {
    // 
    this.$vux.loading.show({
    text: '...'
    })
    
    //if(this.$store.state.applies.length==0){
     // 
        this.$http({
            method:'POST',
            url:this.$store.state.domain+'api/XiechengJobAPI/GetApplies',
            body:{},
            headers: {"Authorization": this.$store.state.jwt.token}
            })
        .then((d) => {
                if(d.data.success){
                  this.base.personName=d.data.content.personName
                  this.items=d.data.content.items
                  this.$store.state.applies=this.items;
                  for(var i=0;i<this.items.length;i++){
                    this.joblist.push({key:this.items[i].id,value:this.items[i].jobName})// 
                  }
                  this.selectjob=this.joblist[this.$store.state.jobIdx].key//
                  //this.getPhase(this.selectjob);
                  // if(this.items[0].id){
                  //   this.$http({
                  //       method:'POST',
                  //       url:this.$store.state.domain+'/api/XiechengApplicantApplySetAPI/GetProcessPhases',
                  //       body:{"applyId": this.items[0].id},
                  //       headers: {"Authorization": this.$store.state.jwt.token}
                  //       })
                  //   .then((d) => {

                  //   }, (d) => {
                  //     alert('');
                  //   });
                  // }
                }else{
                  this.globalMes=d.data.message;
                  this.describeTxt='--';
                  this.showPosition(d.data.message);
                }
                this.$vux.loading.hide()
              }, (d) => {
                this.showPosition('');
        });
     // }else{
            //this.items=this.$store.state.applies;
            //for(var i=0;i<this.items.length;i++){
             // this.joblist.push({key:this.items[i].id,value:this.items[i].jobName})// 
            //}
            //this.selectjob=this.joblist[this.$store.state.jobIdx].key//
     // }

    //
    this.$watch('selectjob', function (newVal, oldVal) {
      for(var i=0;i<this.joblist.length;i++){
        if(this.joblist[i].key==newVal){
          this.jobIdx=i
          this.$store.state.jobIdx=i
          break
        }
      }
      this.getPhase(newVal);
    })
  },
  filters: {  
    formatTime: function (value,sta1,sta2) {
      if(value){
        if(value.interviewTime==null || !value.interviewTime){
          return '';
        }else{
          var dateee = new Date(value.interviewTime).toJSON(); 
          return new Date(+new Date(dateee)).toISOString().replace(/T/g,' ').replace(/\.[\d]{3}Z/,'')// +8*3600*1000
        }
      }
    }
  },
  methods:{
    getPhase(id){
        //
        this.$http({
            method:'POST',
            url:this.$store.state.domain+'api/XiechengJobAPI/GetProcessPhases',
            body:{"applyId": id},
            headers: {"Authorization": this.$store.state.jwt.token}
            })
        .then((d) => {
          this.phaseNames=d.data.content.items.map(function(item,index,array){
            return item.name;
          })
          this.statusName=this.items[this.jobIdx].statusName;
          this.phaseName=this.items[this.jobIdx].phaseName;
          for(var i=0;i<this.phaseNames.length;i++){
            if(this.phaseNames[i]==this.phaseName){
              this.levIdx=i
              break
            }
          }
          //
          if(this.levIdx>1  && this.levIdx<this.phaseNames.length-1){
            if(!this.items[this.jobIdx].interviewTime && this.statusName==('' || '')){
              this.describeTxt='';
            }else{
              this.describeTxt='';
            }
          }else{
            this.describeTxt='--';
          }
        }, (d) => {
            this.showPosition('');
        });
    },
    go(index){
      if(index>1 && index==this.levIdx && !this.items[this.jobIdx].interviewTime && this.statusName==('' || '')){
        if(index==2){
          this.$store.state.isFirstTry=true;//
        }else{
          this.$store.state.isFirstTry=false;//
        }
        this.$router.push("/describe/"+this.items[this.jobIdx].id);
      }else{
        return false;
      }
    },
    onCancel () {
      console.log('on cancel');
    },
    onConfirm () {
      this.showPositionValue = true
      this.$http({
          method:'POST',
          url:this.$store.state.domain+'api/XiechengJobAPI/RemoveApplyInterviewTime',
          body:{"applyId": this.items[this.jobIdx].id},
          headers: {"Authorization": this.$store.state.jwt.token}
          })
      .then((d) => {
              if(d.data.success){
                this.showPosition('!');
                this.describeTxt='';
                this.items[this.jobIdx].interviewTime=null;
              }else{
                this.showPosition(d.data.message);
              }
            }, (d) => {
              this.showPosition('');
      });
    },
    onHide () {
      console.log('on hide')
    },
    onShow () {
      console.log('on show')
    },
    showPlugin () {
      if(this.describeTxt=='--'){
        return false;
      }
      if(this.describeTxt==''){
         this.$router.push("/describe/"+this.items[this.jobIdx].id);
      }else{
        this.show=true;
      }
    },
     showPosition (t) {
      this.showPositionValue = true;
      this.tips=t;
    },
  }
}



